





















import amap from '../../../common/amap-wx.js';
export default {
	data() {
		return {
			hasLocation: false,
			location: {},
			amapPlugin: null,
			key: '044ac57e5a765e13efbe28f63077ec15',
			addressName: '',
			weather: {
				hasData: false,
				data: []
			},
			allAddress: ''
		}
	},
	onLoad() {
		this.amapPlugin = new amap.AMapWX({
			key: this.key
		});
	},
	methods: {
		getRegeo() {
			uni.showLoading({
				title: '定位中'
			});
			this.amapPlugin.getRegeo({
				success: (data) => {
					console.log(JSON.stringify(data))
					this.addressName = data[0].regeocodeData.addressComponent;
					this.allAddress = data[0].regeocodeData.formatted_address;
					console.log(this.addressName.province)
					console.log(this.addressName.district)
					uni.hideLoading();
				}
			});
		},
		getWeather() {
			uni.showLoading({
				title: '获取信息中'
			});
			this.amapPlugin.getWeather({
				success: (data) => {
					console.log(data);
					for (const key in data) {
						console.log(key);
						if (key !== 'liveData') {
							this.weather.data.push({
								name: 'div',
								children: [{
									type: 'text',
									text: data[key].text + '：' + data[key].data
								}]
							});
						}
					}
					uni.hideLoading();
					this.weather.hasData = true;
				}
			});
		}
	}
}
